/**
 *
 * *** Global ***
 * 	Defalut
 * 	Typography
 * 	Sections Common
 * 
 * *** Main Menu ***
 * 
 * *** About ***
 *
 * *** Skills ***
 *
 * *** Portfolio ***
 *
 * *** Experience ***
 *
 * *** Resume ***
 *
 * *** Contact ***
 * 
 * *** BackDrop Support ***
 * 
 * *** Mobile Device ***
 *
 */
/**
 * == Global ==
 *	Default
 */
*
{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

::-moz-selection
{
    color: #fff; 
    background-color: rgba(0, 0, 0, .8);
}

::selection
{
    color: #fff; 
    background-color: rgba(0, 0, 0, .8);
}

body
{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
}

body.page-template-custom-front-page
{
    overflow: hidden; 

    background-color: #000318;
}

.container
{
    position: relative;

    display: flex;
    overflow-x: hidden; 

    height: 100vh;

    justify-content: center;
    align-items: center;
}

.body-bg-image
{
    position: absolute;
    z-index: -2;

    width: 100%;
    height: 100%;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.body-bg-image:before
{
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    content: '';

    background-color: rgba(0, 0, 0, .45);
}

/**
 * == Global ==
 *	Typography
 */
h1
{
    font-size: 32px;
}

h2
{
    font-size: 25px;
}

h3
{
    font-size: 23px;
}

h4
{
    font-size: 21px;
}

p
{
    font-size: 16px;
}

/**
 * == Global ==
 *	Sections Common
 */
.content-wrapper
{
    bottom: -300px;

    overflow: hidden;

    width: 0;
    height: 0;

    transition: transform .5s, bottom .3s; 
    transform: rotateX(8deg) rotateY(70deg) scale(1.25);
    transform-origin: 50% 50%;

    color: #272727;
}
.content-wrapper.show-content
{
    position: fixed;
    bottom: 50%;

    width: 65vw;
    height: 75vh;

    transform: rotateX(0deg) rotateY(0deg) translateY(50%) scale(1);

    background-color: rgba(255, 255, 255, .94);
}
.content-wrapper .content-title,
.content-wrapper .content-close
{
    color: #151515;
}

.content-header
{
    position: sticky;
    top: 0;

    display: flex;

    padding: 0 20px; 

    background-color: rgba(255, 255, 255, .6);

    justify-content: space-between;
    align-items: center;
}

.content-inner-wrapper
{
    overflow-y: auto; 

    height: 100%;
    padding: 60px 110px 0;
}
.content-inner-wrapper::-webkit-scrollbar
{
    width: 6px;
}
.content-inner-wrapper::-webkit-scrollbar-track
{
    background: rgba(255, 255, 255, .1);
}
.content-inner-wrapper::-webkit-scrollbar-thumb
{
    border-radius: 20px; 
    background-color: #3c93eb;
}

.card-wrapper
{
    margin-bottom: 100px;
}

.content-close
{
    font-size: 22px;

    display: inline-block;

    padding: 12px; 

    cursor: pointer;
}

/**
 * == Main Menu ==
 */
.main-menu
{
    position: fixed;
    bottom: 30px;
}
.main-menu:after,
.main-menu:before
{
    position: absolute;

    display: block;

    width: 400px;

    content: '';
}
.main-menu:before
{
    bottom: 25px;
    left: 50%;

    height: 25px;

    transform: translateX(-50%) rotateX(45deg);

    background-color: rgba(153, 153, 153, .4); 

    clip-path: polygon(8% 0%, 95% 0%, 100% 100%, 0% 100%);
}
.main-menu:after
{
    bottom: 23px;
    left: 50%;

    height: 6px;

    transform: translateX(-50%); 

    background-color: rgba(195, 195, 195, .55);
}
.main-menu nav
{
    position: absolute;
    bottom: 30px;
    left: 50%;

    transition: all .2s;
    transition: bottom .3s;
    transform: translateX(-50%);
}

.main-menu__list
{
    display: flex;

    justify-content: center;
}

.main-menu__item
{
    font-size: 40px;

    position: relative;

    margin-right: 35px;

    list-style: none;

    cursor: pointer;
    transition: transform .3s ease-in; 
    transform: scale(1);

    color: #fff;
}
.main-menu__item.active::after
{
    position: absolute;
    bottom: -8px;
    left: 50%;

    width: 8px;
    height: 8px;

    content: '';
    transform: translateX(-50%); 

    border-radius: 50%;
    background-color: #fff;
}
.main-menu__item:last-child
{
    margin-right: 0;
}
.main-menu__item:hover
{
    transform: scale(1.5) translateY(-10px);
}

.main-menu nav.menu-scale
{
    bottom: 65px;

    transform: translateX(-50%) scale(1.4);
}

.nav-title
{
    position: fixed;
    right: -100px;
    bottom: 20px;

    transition: right .1s ease-in; 

    color: #fff;
    border-radius: 2px;
    background-color: rgba(230, 230, 230, .4);
    box-shadow: 2px 1px 2px rgba(0, 0, 0, .7);
}
.nav-title.show-title
{
    font-size: 18px;
    font-weight: 550;

    right: 0;

    width: 100%;
    max-width: 215px;
    padding: 5px 0 5px 15px;

    letter-spacing: 1px;
}

/**
 * == About ==
 */
.about
{
    max-width: 435px; 

    text-align: center;

    color: #fff;
}
.about .profile-image
{
    width: 100px;
    height: 100px;
    margin-bottom: 15px;

    transition: border-radius 1s; 

    opacity: 0;
    border-radius: 50%;
}
.about .profile-image.show
{
    opacity: 1;
}
.about .name
{
    margin-bottom: 2px;
}
.about p
{
    font-size: 12px;

    color: #ddd;
}

.hr-wrapper
{
    position: relative;

    margin-top: 30px;
}
.hr-wrapper .hr
{
    position: absolute;
    left: 50%;

    height: 0;

    transition: height .2s ease-in; 
    transform: rotate(180deg) translateX(-50%);

    opacity: 0;
    color: #fff;
    border: 0;
    box-shadow: 1px 2px 10px 1px #f78f87;
}

/**
 * == Skills ==
 */
.professional-skills
{
    margin-bottom: 60px;

    transition: opacity 2.4s; 

    opacity: 0;
}

.genarel-skills
{
    transition: opacity 3.7s; 

    opacity: 0;
}

.skill-title
{
    font-size: 21px;
}

.content-detail
{
    margin-top: 10px;
}

.skill-item p
{
    font-size: 15px;
    line-height: 1.5;
}

/**
 * == Portfolio ==
 */
.portfolio .card-wrapper
{
    display: grid;

    grid-template-columns: repeat(2, 1fr);
    grid-gap: 80px;
    grid-row: 1/3;
    grid-column: 2;
}

.portfolio .work-card
{
    position: relative; 

    display: flex;

    padding: 20px 25px;

    transition: opacity .2s ease-in;

    opacity: 0;
    border-radius: 4px;
    background-color: rgba(60, 54, 54, .15);
}
.portfolio .work-card .work-media
{
    position: absolute;
    top: 10px;
    right: 15px;
}
.portfolio .work-card .work-media img
{
    width: 65px;
    height: 60px;

    border-radius: 5px;
}
.portfolio .work-card .work-content
{
    width: 100%;
}
.portfolio .work-card .work-content .short-description
{
    font-size: 14px;
    line-height: 1.5;
}
.portfolio .work-card .work-meta
{
    font-size: 12px; 

    margin-bottom: 15px;
}
.portfolio .work-card .work-meta a
{
    font-size: 16px;

    padding: 6px; 

    color: #272727;
}
.portfolio .work-card .work-title
{
    font-size: 18px;

    margin-bottom: 5px;
}
.portfolio .work-card .work-title a
{
    text-decoration: none; 

    color: #272727;
}
.portfolio .work-card .work-title a:hover
{
    text-decoration: underline;
}

/**
 * == Experience ==
 */
.content-wrapper.show-content .job-wrapper
{
    margin-bottom: 80px;
}

.job-detail
{
    margin-bottom: 70px;

    transition: opacity .8s; 

    opacity: 0;
}
.job-detail:last-child
{
    margin-bottom: 0;
}
.job-detail .designation
{
    display: flex;

    margin-bottom: 10px;

    align-items: center;
}
.job-detail .designation h6,
.job-detail .designation span
{
    font-size: 12px;

    margin-top: 4px;
    margin-left: 10px;
}
.job-detail .work-duration
{
    font-size: 14px;

    color: #595959;
}
.job-detail .responsibilities
{
    margin-top: 18px;
}
.job-detail .responsibilities ul
{
    padding-left: 25px;
}
.job-detail .responsibilities li
{
    margin-bottom: 10px;
}
.job-detail .responsibilities li:last-child
{
    margin-bottom: 0;
}

.resume-wrapper
{
    position: relative;

    margin-bottom: 100px; 

    text-align: center;
}

.resume-wrapper img
{
    width: auto;
    max-width: 100%;

    transition: opacity .3s ease-in; 
    transition-delay: .3s;

    opacity: 0;
}

.resume-download
{
    position: fixed;
    z-index: 2;
    top: 102px;
    right: 160px;

    padding: 7px 8px;

    transition: transform .3s ease-in; 
    transition-delay: .65s;
    transform: scale(0);
    text-decoration: none;
}
.resume-download i
{
    font-size: 30px;

    color: #02b702;
    box-shadow: 1px 3px 8px rgba(0, 0, 0, .45);
}
.resume-download:hover i
{
    color: #028802;
}

/**
 * == Contact ==
 */
.contact .content-inner-wrapper
{
    display: flex;
    flex-direction: column;

    align-items: center;
}

.contact .social-media
{
    display: flex;

    margin-bottom: 50px;
}
.contact .social-media li
{
    margin-right: 10px;

    list-style: none;

    transition: transform .1s ease-in; 
    transform: scale(0);
}
.contact .social-media li a
{
    font-size: 30px;

    display: block;

    padding: 10px;

    text-decoration: none; 

    color: #f0c005;
}

.contact .contact-detail
{
    display: flex;

    transition: transform .4s ease-in; 
    transform: scale(0) rotateY(-35deg);

    align-items: center;
}

.contact .email-contact
{
    display: flex;

    align-items: center;
}
.contact .email-contact p
{
    margin-right: 5px;
}
.contact .email-contact a
{
    font-size: 25px;

    display: inline-block;

    padding: 0 6px;

    text-decoration: none; 

    color: #f0c005;
}

.contact .messenger-contact
{
    display: flex;

    margin-left: 20px; 

    align-items: center;
}
.contact .messenger-contact p
{
    margin-right: 5px;
}
.contact .messenger-contact a
{
    font-size: 25px;

    display: inline-block;

    padding: 0 6px;

    text-decoration: none; 

    color: #f0c005;
}

.contact .blog-detail
{
    margin-top: 15px;

    transition: opacity 1.2s ease-in; 

    opacity: 0;
}
.contact .blog-detail .blog-link
{
    margin-right: 10px;
}
.contact .blog-detail p
{
    display: flex;

    align-items: center;
}
.contact .blog-detail a
{
    font-size: 25px;

    margin-left: 8px;

    text-decoration: none; 

    color: #f0c005;
}

/**
 * == BackDrop Support ==
 */
@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none)
{
    .content-wrapper.show-content
    {
        color: #fff;
        background-color: rgba(25, 50, 119, .2);
        text-shadow: 1px 1px rgba(41, 41, 41, .8);

        backdrop-filter: blur(15px);
    }
    .content-wrapper .content-inner-wrapper::-webkit-scrollbar-track
    {
        background-color: rgba(4, 13, 21, .1);
    }
    .content-wrapper .content-header
    {
        background-color: rgba(84, 126, 239, .1);
    }
    .content-wrapper .content-title,
    .content-wrapper .content-close
    {
        color: #fff;
        text-shadow: none;
    }
    .main-menu .nav-title
    {
        background-color: rgba(152, 179, 253, .3);

        backdrop-filter: blur(1px);
    }
    .social-media li a
    {
        text-shadow: 1px 2px rgba(15, 15, 15, .7);
    }
    .skill-item .skill-title
    {
        color: #fdbcc9;
    }
    .portfolio .work-card
    {
        background-color: rgba(4, 13, 21, .1);
    }
    .portfolio .work-card .work-duration
    {
        color: #ddd;
    }
    .portfolio .work-card .work-title a
    {
        color: #fdbcc9;
    }
    .portfolio .work-card .work-meta a
    {
        color: #fff;
    }
    .experience .job-detail .designation
    {
        color: #fdbcc9;
    }
    .experience .job-detail .work-duration
    {
        font-size: 14px;

        color: #d4d2d2;
    }
}

/**
 * == Mobile Device ==
 */
@media (max-width: 767.95px)
{
    .about
    {
        padding: 0 12px;
    }
    .content-wrapper.show-content
    {
        bottom: 55%; 

        width: 95vw;
        height: 74vh;
    }
    .content-wrapper.show-content .content-header
    {
        padding: 0 10px 0 20px;
    }
    .content-wrapper.show-content .content-inner-wrapper
    {
        padding: 40px 20px 0;
    }
    .content-wrapper.show-content .card-wrapper
    {
        margin-bottom: 80px;
    }
    .main-menu
    {
        width: 100vw;
    }
    .main-menu:before,
    .main-menu:after
    {
        width: 95%;
    }
    .main-menu .main-menu__item
    {
        font-size: 35px;

        margin-right: 30px;
    }
    .main-menu .main-menu__item:hover
    {
        transform: scale(1) translateY(0px);
    }
    .main-menu .main-menu__item:last-child
    {
        margin-right: 0;
    }
    .portfolio .card-wrapper
    {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 40px;
    }
    .portfolio .work-card
    {
        position: relative;

        padding: 20px 15px;
    }
    .portfolio .work-card .work-media
    {
        position: absolute;
        top: 18px;
        right: 15px;
    }
    .portfolio .work-card .work-media img
    {
        width: 50px;
        height: 45px;
    }
    .resume .resume-download
    {
        top: 100px;
        right: 40px;

        padding: 5px 6px;
    }
    .resume .resume-download i
    {
        font-size: 24px;
    }
    .contact .contact-detail
    {
        flex-direction: column;

        align-items: flex-start;
    }
    .contact .messenger-contact
    {
        margin-top: 12px; 
        margin-left: 0;
    }
    .contact .messenger-contact span
    {
        margin-left: 14px;
    }
    .contact .blog-detail
    {
        margin-top: 30px;
    }
    .main-menu.menu-scale nav
    {
        transform: scale(1.2);
    }
    .main-menu .nav-title
    {
        display: none;
    }
}
